/* eslint-disable */

import { gsap } from "gsap/dist/gsap";
import { DrawSVGPlugin } from "../vendors/gsap/DrawSVGPlugin";
import { SplitText} from "../vendors/gsap/SplitText";

gsap.registerPlugin(DrawSVGPlugin, SplitText);

class Hero {
  constructor(el) {
    this.el = el;
    this.init();
    this.initEvents();
    this.setup();
    this.animate();
    this.play();
  }

  init() {
    this.title = document.getElementById('hero-title')
    this.hero = document.querySelector('.amd-hero')
    this.grid = document.getElementById('hero-grid')
    this.gridLines = [...this.grid.querySelectorAll('path')]
    this.plant = document.getElementById('hero-plant')
    this.atom = document.getElementById('hero-atom')
    this.people = document.getElementById('hero-people')
    this.peopleImg = this.people.querySelector('img')
    this.scientist = document.getElementById('hero-scientist')
    this.scientistImg = this.scientist.querySelector('img')
    this.space = document.getElementById('hero-space')
    this.circles = document.getElementById('hero-circles')
    this.dots = document.getElementById('hero-dots')
    this.dotsLines = [...this.dots.querySelectorAll('path')]
    this.blueBox = document.getElementById('hero-blue-box')
    this.boxLines = [...this.blueBox.querySelectorAll('path')]
    this.label50 = document.getElementById('hero-50');
    this.label50Img = this.label50.querySelector('img');
    this.circleYellow = document.getElementById('hero-circle-yellow');
    this.circleGreen = document.getElementById('hero-circle-green');
    this.circleMarina = document.getElementById('hero-circle-marina');
    this.circleMarinaInner = document.getElementById('hero-circle-marina--inner');
    this.ship = document.getElementById('hero-ship');
    this.shipImg = this.ship.querySelector('img')
    this.code = document.getElementById('hero-code');
    this.codeLines = [...this.code.querySelectorAll('svg > *')]
    this.tl = gsap.timeline({play: false})
    this.tlCode = gsap.timeline({play: false, repeat: -1, delay: 1})
  }

  setup() {

    this.titleLetters = new SplitText(this.title, { type: 'words, chars', charsClass: 'js-letter' }).chars;
    this.titleLetters.forEach((letter, i) => {
      return letter.innerHTML = '<div class="js-inner">' + letter.innerHTML + '</div>'
    })

    this.titleLettersInner = [...this.title.querySelectorAll('.js-inner')]
    gsap.set(this.blueBox, { scale: 0.1, opacity: 0 })
    gsap.set(this.gridLines, { drawSVG: 0 })
    gsap.set(this.dotsLines, { scale: 0 })
    gsap.set(this.circleGreen, { scale: 0 })
    gsap.set(this.circleYellow, { scale: 0 })
    gsap.set(this.plant, { scale: 0, x: "+=20", y: "+=20" })
    gsap.set(this.atom, { opacity: 0, x: "-=200" })
    gsap.set(this.scientist, { opacity: 0, x: "-=200" })
    gsap.set(this.space, { opacity: 0, y: "-=100" })
    gsap.set(this.circles, { opacity: 0, y: "-=50" })
    gsap.set(this.label50, { opacity: 0, x: "+=200" })
    gsap.set(this.people, { opacity: 0, x: "+=200" })
    gsap.set(this.ship, { perspective: 800 })
    gsap.set(this.circleMarina, { perspective: 800 })
    gsap.set(this.circleMarinaInner, { opacity:0, transformOrigin: '50% 100%', rotateX: 275 })
    gsap.set(this.shipImg, { opacity:0, transformOrigin: "50% 100%", rotateX: 275 })
    gsap.set(this.codeLines, { opacity: 0 })
  }

  animate() {
    gsap.set(this.title, { opacity: 1 })
    gsap.to(this.el, { autoAlpha: 1, duration: 0.1 })
    this.tl
      .to(this.blueBox, { scale: 1, opacity: 1, duration: 0.7, ease: 'power2.out' }, 0)
      .to(this.gridLines, { drawSVG: '100%', stagger: 0.05, duration: 1.5, ease: 'power2.out' }, 0.2)
      .to(this.dotsLines, { scale: 1, stagger: { amount: 1.5, grid: [8, 8], from: 0 }, duration: .5, ease: 'power2.out' }, 0.5)
      .to(this.scientist, { opacity: 1, x: 0, duration: 1, ease: 'power2.out' }, 0.8)
      .to(this.people, { opacity: 1, x: 0, duration: 1, ease: 'power2.out' }, 0.9)
      .to(this.circleGreen, { scale: 1, duration: 1.5, ease: 'elastic.out(1, 0.55)' }, 1)
      .to(this.titleLettersInner, { y: '0%', stagger: 0.05, duration: .3, ease: 'power2.out' }, 1)
      .to(this.circleYellow, { scale: 1, duration: 1.5, ease: 'elastic.out(1, 0.45)' }, 1.2)
      .to(this.space, { opacity: 1, y: 0, duration: 1, ease: 'power2.out' }, 1.3)
      .to(this.atom, { opacity: 1, x: 0, duration: 1, ease: 'power2.out' }, 1.5)
      .to(this.label50, { opacity: 1, x: 0, duration: 1, ease: 'power2.out' }, 1.5)
      .to(this.plant, { scale: 1, x: 0, y: 0, duration: 0.7, ease: 'power2.out' }, 2)
      .to(this.circleMarinaInner, { opacity: 1, duration: .1, ease: 'power2.out' }, 2)
      .to(this.circleMarinaInner, { rotateX: 360, duration: .7, ease: 'power2.out' }, 2)
      .to(this.shipImg, { opacity: 1, duration: .1, ease: 'power2.out' }, 2.5)
      .to(this.shipImg, { rotateX: 360, duration: .7, ease: 'power2.out' }, 2.5)
      .to(this.circles, { opacity: 1, y: 0, duration: .7, ease: 'power2.out' }, 2.8)

    this.tlCode
      .to(this.codeLines, { opacity: 1, stagger: 0.03, duration: 0.07, ease: 'power2.out' }, 1.5)
      .to(this.codeLines, { opacity: 0, stagger: {  each: -0.01 },  duration: 0.05, ease: 'power2.out' }, "+=3")
  }

  play() {
    this.tl.play()
    this.tlCode.play()
  }

  onResize() {
    this.width = window.innerWidth;

    // fix for mobile
    if (this.widthPrev === this.width) return;
    this.widthPrev = this.width

    this.resize();
  }

  parallaxIt(target, movement) {
    gsap.to(target, {
      duration: 0.5,
      x: (this.mouse.x - this.rect.width / 2) / this.rect.width * movement,
    });
  }

  resize() {
    this.rect = this.hero.getBoundingClientRect();
  }

  initEvents() {
    this.mouse = {x: 0, y: 0, moved: false};

    this.resize();

    window.addEventListener('resize', this.onResize.bind(this));

    this.hero.addEventListener('mousemove', (e) => {
      this.mouse.x = e.clientX - this.rect.left;
      this.mouse.moved = true;
    });

    gsap.ticker.add(() =>{
      if (this.mouse.moved){
        this.parallaxIt(this.label50Img, -50);
        this.parallaxIt(this.scientistImg, -30);
        this.parallaxIt(this.peopleImg, -20);
        this.parallaxIt(this.dots, 10);
        this.parallaxIt(this.grid, 10);
        this.parallaxIt(this.circleMarina, -10);
        this.parallaxIt(this.ship, -5);
      }
      this.mouse.moved = false;
    });
  }
}

export default Hero;

/* eslint-enable */
