/* eslint-disable */

import Map from "./classes/Map";
import Filters from "./classes/Filters";
import {initShare, initSmoothScroll} from "./classes/Helpers";
import Hero from "./classes/Hero";
import AnimatedAppearingScroll from "./classes/AnimatedAppearingScroll";

document.addEventListener("DOMContentLoaded", function(event) {
  const mapEl = document.getElementById('ad-map')
  const btnTocOpener = document.querySelectorAll('.amd-toc-opener');
  const filtersEl = document.getElementById('ad-filters')
  const hero = document.getElementById('amd-hero-scene');
  if(mapEl) {
    new Map(mapEl);
  }

  if(filtersEl) {
    new Filters(filtersEl);
  }

  if(btnTocOpener) {
    [...btnTocOpener].forEach((el) => {
      el.addEventListener('click', (e) => {
        e.preventDefault();
        document.body.classList.toggle('sticky-aside-active');
      })
    })
  }

  if(hero) {
    new Hero(hero);
  }

  new AnimatedAppearingScroll();

  initSmoothScroll();
  initShare();
});

/* eslint-enable */
