/* eslint-disable */

export const initShare = () => {

  const getShareLink = (link, title, share) => {
    const templates = {
      'twitter' : 'https://twitter.com/intent/tweet?url=%link%&text=%title%',
      'linkedin' : 'https://www.linkedin.com/shareArticle?mini=true&url=%link%&title=%title%',
      'facebook' : 'https://www.facebook.com/sharer/sharer.php?u=%link%',
      'hackernews' : 'http://news.ycombinator.com/submit?link=%link%',
      'whatsapp' : 'https://api.whatsapp.com/send?text=%title%-%link%',
      'flipboard' : 'https://share.flipboard.com/bookmarklet/popout?v=2&title=%title%&url=%link%',
      'reddit' : 'https://www.reddit.com/submit?url=%link%&title=%title%',
      'pinterest' : 'http://pinterest.com/pin/create/button/?url=%link%&description=%title%'
    };

    return templates[share].replace('%link%', link).replace('%title%', title);
  };

  var Config = {
    Link: "a.share",
    Width: 500,
    Height: 500
  };

  $('[data-share-title]').on('click', function (e) {
    var item = $(this);
    e.preventDefault();
    const link = encodeURIComponent(item.attr('href'));
    const title = encodeURIComponent(item.data('shareTitle'));
    const type = item.data('share');

    const popupLink = getShareLink(link, title, type);

    var
      px = Math.floor(((screen.availWidth || 1024) - Config.Width) / 2),
      py = Math.floor(((screen.availHeight || 700) - Config.Height) / 2);

    var popup = window.open(popupLink, "social",
      "width="+Config.Width+",height="+Config.Height+
      ",left="+px+",top="+py+
      ",location=0,menubar=0,toolbar=0,status=0,scrollbars=1,resizable=1");
    if (popup) {
      popup.focus();
      if (e.preventDefault) e.preventDefault();
      e.returnValue = false;
    }
  });

  $('[data-share-link]').on('click', (e) => {
    e.preventDefault();
    var input = document.createElement('textarea');
    var shareParent = $(e.currentTarget).parents('.social-networks');
    if(shareParent.length) {
      var shareButton = shareParent.find('.share-opener');
      if(shareButton.length) {
        var defaultToolTipText = shareButton.text();
        shareButton.text('Copied!');

        setTimeout(function(){
          shareButton.text(defaultToolTipText);
        }, 1000);
      }
    }

    input.innerHTML = $(e.currentTarget).attr('href');
    document.body.appendChild(input);
    input.select();
    var result = document.execCommand('copy');
    document.body.removeChild(input);
  });
};

export const initSmoothScroll = () => {
  $('a.smooth-scroll').on('click', function (e) {
    e.preventDefault();
    const target = $(this).attr('href');
    $('html, body').animate({
      scrollTop: $(target).offset().top
    }, 500);
  });
}

/* eslint-enable */
