class AnimatedAppearingScroll {
  constructor(el) {
    this.bindMethods();
    this.el = el || document.body;
    this.cache = null;
    this.options = null;
    this.observer = null;
    this.init()
  }

  bindMethods() {
    ['handler']
      .forEach((fn) => this[fn] = this[fn].bind(this))
  }

  initItems() {
    this.itemsBlock = Array.from(this.el.querySelectorAll('[data-appear-block], [data-appear-dash], [data-appear-stagger], [data-svg-animation], [data-appear-group], [data-appear-illustration], [data-appear-yt-play], [data-counter-num]'));
  }

  inlineSVG() {
    Array.from(this.el.querySelectorAll('[data-appear-illustration]')).forEach((el) => {
      if (el.dataset.appearIllustration != null) {
        setTimeout(() => {
          this.drawSVG(el);
        }, 300);
      }
    })
  }

  init() {
    this.initItems();
    this.getCache()
    this.inlineSVG()
    this.createObserver()
    this.run()
  }

  run() {
    this.cache.forEach(elem => {
      this.observer.observe(elem.el)
    })
  }

  createObserver() {
    this.options = {
      root: null,
      rootMargin: '0px 0px -25% 0px',
      threshold: [0, 0]
    }
    this.observer = new IntersectionObserver(this.handler, this.options)
  }

  drawSVG(el) {
    if (el.dataset.appearIllustration === 'curve_chart_1') {
      const line = el.querySelector('.chart-line');
      const dots = [...el.querySelectorAll('.chart-dot')];
      gsap.set(line, {drawSVG: "0%"});
      gsap.set(dots, {opacity: 0});
      const timeline = gsap.timeline({paused: true});
      timeline
        .to(line, {duration: 2, drawSVG: "100%", ease: "none"}, 0)
        .to(dots, {duration: 0.3, opacity: 1, stagger: 0.4, ease: "none"}, 0)

      el.timeline = timeline;
    }

    if (el.dataset.appearIllustration === 'curve_chart_2') {
      const lines = [...el.querySelectorAll('.chart-line')];
      gsap.set(lines, {drawSVG: "0%"});
      const timeline = gsap.timeline({paused: true});
      timeline
        .to(lines, {duration: 2, drawSVG: "100%", ease: "none"}, 0)

      el.timeline = timeline;
    }

    if (el.dataset.appearIllustration === 'bar_chart') {
      const note = el.querySelector('.chart-notes');
      const lines = [...el.querySelectorAll('rect')];

      gsap.set(note, {opacity: 0});
      lines.forEach((line) => {
        line.setAttribute('data-height', line.getAttribute('height'));
        gsap.set(line, {height: 0, transformOrigin: "50% 50%", rotation: 180});
      });

      const timeline = gsap.timeline({
        paused: true,
        onComplete: () => {
          gsap.to(note, {duration: 0.3, opacity: 1, ease: "none"});
        }
      })

      lines.forEach((line, id) => {
        timeline
          .to(line, {
            duration: 2,
            height:(el)=> {
              return line.dataset.height;
            }, ease: "none"}, id * 0.1)
      })

      el.timeline = timeline;
    }

    if (typeof el.mustplay !== 'undefined' && el.timeline) {
      el.timeline.play();
    }
  }

  getCache() {
    this.cache = []
    this.svgAnimations = []
    this.itemsBlock.forEach((el, index) => {
      this.cache.push({
        el: el,
        isIntersected: false,
        elems: null,
      })

      // if(typeof el.dataset.appearStagger !== 'undefined') {
      //     const delay = (parseFloat(el.dataset.animationDelay) >= 0) ? parseFloat(el.dataset.animationDelay) : 0.15;
      //     Array.from(el.children).forEach((elem, index) => {
      //         elem.style.transitionDelay = (index * delay) + 's'
      //     });
      // }

      // if(typeof el.dataset.appearGroup !== 'undefined') {
      //     const delay = (parseFloat(el.dataset.animationDelay) >= 0) ? parseFloat(el.dataset.animationDelay) : 0.15;
      //     Array.from(el.querySelectorAll('[data-appear-group-item]')).forEach((elem, index) => {
      //         elem.style.transitionDelay = (index * delay) + 's'
      //     });
      // }

      // if(typeof el.dataset.counterNum !== 'undefined') {
      //     el.counter = new CounterNums(el)
      // }
    })
  }

  handler(entries) {
    entries.forEach(entry => {
      if (entry.isIntersecting) {
        let i = this.itemsBlock.indexOf(entry.target)
        let elem = this.cache[i]
        elem.isIntersected = true
        elem.el.classList.add('is-appeared');

        // if(typeof elem.el.dataset.counterNum !== 'undefined') {
        //     elem.el.counter.run()
        // }

        if (typeof elem.el.dataset.appearIllustration !== 'undefined') {
          if (typeof elem.el.timeline !== 'undefined') {
            setTimeout(() => {
              elem.el.timeline.play();
            }, 300)
          } else {
            elem.el.mustplay = true;
          }
        }

        this.stillObserving() ? this.observer.unobserve(entry.target) : this.observer.disconnect()
      } else {
        return
      }
    })
  }

  stillObserving() {
    return this.cache.some(e => !e.isIntersected)
  }

  destroy() {
    this.observer.disconnect()
    this.observer = null
    this.elems = null
    this.cache = null
  }
}

export default AnimatedAppearingScroll

