/* eslint-disable */

class Filters {
  constructor(el) {
    this.el = el;
    this.filters = [...this.el.querySelectorAll('[data-filter-select]')];
    this.filterItems = [...document.querySelectorAll('[data-filter]')];
    this.cta = [...document.querySelectorAll('.widget--subscribe-inline')];
    this.filters.forEach(filter => {
      filter.addEventListener('click', (e) => {
        this.filter(e);
      });
    });
  }

  filter(e) {
    e.preventDefault();
    const filterEl = e.currentTarget;
    filterEl.classList.toggle('active');

    const filterTypes = [];
    this.filters.forEach(filter => {
      if (filter.classList.contains('active')) {
        filterTypes.push(filter.getAttribute('data-filter-select'));
      }
    });

    if(filterTypes.length === 0) {

      this.filterItems.forEach(item => {
        item.classList.remove('hidden');
      });
      this.cta.forEach(item => {
        item.classList.remove('hidden');
      })
    } else {
      this.cta.forEach(item => {
        item.classList.add('hidden');
      })

      this.filterItems.forEach(item => {
        const filters = item.getAttribute('data-filter');
        const filtersArray = filters.split(',');

        let hideItem = true;

        filterTypes.forEach(filter => {
          if(filtersArray.includes(filter)) {
            hideItem = false;
          }
        });

        item.classList.toggle('hidden', hideItem);
      });

      $('html, body').animate({
        scrollTop: $('#main-content').offset().top
      }, 500);
    }
  }
}

export default Filters;

/* eslint-enable */
